.wrap:before,
.wrap:after {
    content: "";
    position: absolute;
}

.wrap:before {
    width: 40px;
    height: 8px;
    right: 100px;
    background: #840707;
    border-radius: 8px 8px 0px 0px;
}

.wrap:after {
