    }
    .navigation_bar a.close-btn {
        display: none;
    }
}

@media screen and (max-width: 1280px) {
    .navigation_bar ul li a {
        font-size: 13px;
    }
}

@media screen and (max-width: 1199px) {
    .product-image img {
        width: 200px;
        height: 200px;
        object-position: center center;
    }
    .container {
        width: 95% !important;
        max-width: 100%;
    }
    .quantity-cart button {
        padding: 12px 8px;
    }
    .quantity-cart .form-group input {
        width: 45px !important;
        margin-right: 8px;
    }
    ul.price-detail-wrap li:nth-child(2) span {
        font-size: 20px;
    }
}

@media screen and (max-width: 1080px) {
    .quantity-cart {
        display: block;
    }
    .quantity-cart .form-group {
        margin-bottom: 15px;
    }
}

@media screen and (max-width: 1030px) {
    ul.price-detail-wrap li small {
        width: 34px
    }
    ul.price-detail-wrap li:nth-child(2) span {
        font-size: 18px;
    }
}

@media screen and (max-width: 991px) {
    .product-image img {
        width: 160px;
        height: 160px;
    }
    .from-right {
        padding-left: 0px;
        background: #f7f7fa;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
    .from-right ul {
        float: left;
        padding: 0 12px;
        height: 100%;
        flex: 0 0 50%;
        max-width: 50%;
    }
    .from-right ul:last-child {
        margin: 0;
        border: none;
        border-left: 1px solid #e2e5f1;
    }
    .quantity-cart {
        display: flex;
    }
    .quantity-cart .form-group {
        margin-bottom: 0px;
    }
    .quantity-cart button {
        padding: 10px 7px;
        font-size: 12px;
    }
}

